export default {
	data() {
		return {
			data: '',
			load: true,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			unitList: [{
				'uName': '1单元',
				'unitsId': '1',
				'status': '离线中···',
				'type': '2'
			}],
			topTitle:'6幢'
		}
	},
	created() {
		this.getUnitList();
	}
	,
	onReady() {
		uni.hideLoading();
	},
	onLoad(options) {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		if (null != options) {
			var result = JSON.stringify(options);
			var result2 = JSON.parse(result);
			this.topTitle = this.getJsonValue(result2,'inputResult');
		}
	},
	methods:{
		getUnitList(){//根据幢数搜索对应单元数  幢数： this.topTitle
		var self = this;
			const request = uni.request({
				url: self.GLOBAL.baseUrl + 'detailUnitList',
				success(res) {
					self.unitList = res.data.unitList;
				}
			})
		}
		,
		 getJsonValue(obj,name){//obj  json数据  name要获取的值
		        var result = null;
		        var value  = null;
		        for(var key in obj){        
		            value = obj[key];
		            if(key == name){
		                console.log("getjsonvalue:"+value)
		            return value;
		        } else {
		            if( typeof value == "object" ){
		            result = getJsonValue(value,name);
		            }
		        }
		    }
		    return result;
		}
		,
		toDeviceDetail(val, val2) { //跳转设备详情
			console.log("设备buildingID：" + val + "设备单元的id" + val2)
			uni.navigateTo({
				url: `../device/device_detail?buidldingId=${val}&unitId=${val2}`
			})
		},
		openLock(val) {
			var isSuccess = false;
			console.log(val.uName + ":" + val.type + ":" + val.status);
			if (val.type !== "1") {
				uni.showToast({
					image: '../../static/img/ic_offline.png',
					title: "设备已离线"
				})
				return;
			}
			if (isSuccess) {
				uni.showToast({
					image: '../../static/img/ic_success.png',
					title: '开锁成功'
				})
			} else {
				uni.showModal({
					title: '提示',
					cancelColor: '#A8A8A9',
					content: '开锁失败，请重试!',
					confirmColor: '#4C7DFD',
					success(res) {
						if (res.confirm) {
							console.log("点击确认");
						} else {
							console.log("点击取消");
						}
					}
				})
			}
		}
	}	
}
